@use "sass:math";

:root {
    --theme-color: #FF8F1F;
    --title-color: #191825;
    --body-color: #74757B;
    --smoke-color: #F7F7F7;
    --smoke-color2: #E8F3FF;
    --black-color: #000000;
    --black-color2: #080E1C;
    --gray-color: #bdbdbd;
    --white-color: #ffffff;
    --light-color: #bdbdbd;
    --yellow-color: #FFB539;
    --success-color: #28a745;
    --error-color: #dc3545;
    --border-color: #E6E6E6;
    --title-font: 'Raleway', sans-serif;
    --body-font: 'Roboto', sans-serif;
    --icon-font: "Font Awesome 6 Pro";
    --main-container: 1220px;
    --container-gutters: 30px;
    --section-space: 120px;
    --section-space-mobile: 80px;
    --section-title-space: 60px;
    --ripple-ani-duration: 5s
}

.theme-blue {
