/* Gallery.css */

/* General container for the gallery */
.gallery-container {
  text-align: center;
  margin: 20px;
}

.gallery-title {
  font-size: 2rem;
  margin-bottom: 20px;
}

/* Styles for the gallery grid */
.gallery-grid {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 15px;
  margin-top: 20px;
}

/* Styles for each image */
.gallery-image-item {
  width: 300px;
  height: 200px;
  object-fit: cover;
  cursor: pointer;
  border-radius: 8px;
  transition: transform 0.3s ease;
  z-index: 1;
}

.gallery-image-item:hover {
  transform: scale(1.05);
}

/* Modal Styles */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

/* Modal content (image container and buttons) */
.modal-content-container {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: transform 0.5s ease; /* Slide effect */
}

/* Image inside the modal */
.modal-image-container {
  width: 90vw;
  height: 70vh;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.modal-image {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
  transition: transform 0.5s ease;
}



/* Close button styles */
.modal-close-button,
.modal-prev-button,
.modal-next-button {
  position: absolute;
  background: none;
  border: none;
  font-size: 30px;
  color: white;
  cursor: pointer;
  z-index: 1;
}

/* Close button (X) position */
.modal-close-button {
  right: 20px;
  top: 10px;
}

/* Previous and Next button styles */
.modal-prev-button {
  left: 10px;
  top: 50%;
  transform: translateY(-50%);
}

.modal-next-button {
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
}

/* Accessibility: Add hover effects for buttons */
.modal-close-button:hover,
.modal-prev-button:hover,
.modal-next-button:hover {
  color: #ddd;
  transition: color 0.3s ease;
}


